import React, { useEffect } from 'react';
import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';
import { useMachine } from '@xstate/react';
import CssBaseline from '@material-ui/core/CssBaseline';

import * as styles from './App.treat';
import { Insitutions } from './Insitutions';
// import { fetchMachine } from './machines/fetch';
import { storeMachine } from './machines/store';
import { fintrack } from '../typings/fintrack';
import { Assets } from './Assets';
import { AssetAccounts } from './AssetAccounts';
import { AddEditReport } from './AddReport';
import { Home } from './Home';
import { Charts } from './Charts';
import { BottomAppBar } from './BottomAppBar';

export const StoreContext = React.createContext<fintrack.JSONShape | undefined>(
  undefined
);

const App: React.FC = () => {
  const [state1, send, storeService] = useMachine(storeMachine, {
    devTools: true,
  });
  // const [state, send] = useMachine(fetchMachine, {
  //   context: {
  //     url: shouldFetchFail ? 'https://non-existent-url' : '/data.json',
  //     maxRetries: 4,
  //   },
  // });

  // useEffect(() => {
  //   send('LOAD');
  // }, [send]);

  // useEffect(() => {
  //   if (state.matches('failure')) {
  //     send('RETRY');
  //   }
  // }, [state, send]);

  console.log('-----!!', state1.value, state1.context.db?.getState());

  const handleFile = (e: React.ChangeEvent<HTMLInputElement>) => {
    // console.log(
    //   '-----e.target.files[0]',

    // );
    var reader = new FileReader();
    reader.onload = onReaderLoad;
    reader.readAsText(e.target.files![0]);

    function onReaderLoad(event: ProgressEvent<FileReader>) {
      console.log(event.target!.result);
      var obj = JSON.parse(event.target!.result as string);
      // alert_data(obj.name, obj.family);
      send('LOAD_FROM_INPUT', {
        data: obj,
      });
    }

    // e.target.files![0].text().then((v) => {
    //   console.log('------v', v);
    //   send('LOAD_FROM_INPUT', {
    //     data: JSON.parse(v),
    //   });
    // });
    // this.setState({ ...this.state, [e.target.name]: e.target.files[0] });
  };

  return (
    <StoreContext.Provider value={state1.context.db?.getState()}>
      <BrowserRouter>
        <React.Fragment>
          <CssBaseline />

          <div className={styles.root}>
            <div className={styles.nav}>
              <ul>
                <li>
                  <Link to="/">Home</Link>
                </li>
                <li>
                  <Link to="charts">Charts</Link>
                </li>
                <li>
                  <Link to="institutions">Institutions</Link>
                </li>
                <li>
                  <Link to="assets">Assets</Link>
                </li>
                <li>
                  <Link to="asset-accounts">Asset accounts</Link>
                </li>
                <li>
                  <Link to="add-edit-report">Add report</Link>
                </li>
              </ul>
              <button
                onClick={() => {
                  send('SAVE');
                }}
              >
                prepare data
              </button>
              {state1.matches('saving') && (
                <a
                  href={state1.context.urlDataToSave}
                  download="fintrack.json"
                  onClick={() => {
                    setTimeout(() => {
                      send('SAVED');
                    }, 100);
                  }}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  download
                </a>
              )}
              <input type="file" onChange={handleFile} />
            </div>
            <div className={styles.content}>
              <Routes>
                <Route path="charts" element={<Charts />} />
                <Route
                  path="institutions"
                  element={<Insitutions storeService={storeService} />}
                />
                <Route
                  path="assets"
                  element={<Assets storeService={storeService} />}
                />
                <Route
                  path="asset-accounts"
                  element={<AssetAccounts storeService={storeService} />}
                />
                <Route
                  path="add-edit-report/:reportId"
                  element={<AddEditReport storeService={storeService} />}
                />
                <Route
                  path="add-edit-report"
                  element={<AddEditReport storeService={storeService} />}
                />
                <Route element={<Home storeService={storeService} />} />
              </Routes>
            </div>
          </div>
          <BottomAppBar />
        </React.Fragment>
      </BrowserRouter>
    </StoreContext.Provider>
  );
};

export default App;
