import React, { useContext } from 'react';
// import { ResponsiveBump, BumpInputSerie } from '@nivo/bump';
import { ResponsiveLine, Serie } from '@nivo/line';
import { ResponsiveStream } from '@nivo/stream';
import { isWithinInterval, addYears } from 'date-fns';
import { addMonths } from 'date-fns/esm';
import { Link, useSearchParams } from 'react-router-dom';
import _ from 'lodash';

import { StoreContext } from './App';
import { getTotalForReport } from './Home';
import { fintrack } from '../typings/fintrack';

export const Charts: React.FC = () => {
  const store = useContext(StoreContext);
  const [searchParams, setSearchParams] = useSearchParams();

  const currencies: fintrack.Currency[] = ['USD', 'EUR', 'RUB'];

  const fromSearchParam = searchParams.get('yearsBack');
  const pastYear: Interval = {
    // should be last report date and not now
    start: addMonths(
      addYears(new Date(), -1 * parseInt(fromSearchParam ?? '0', 10)),
      -2
    ),
    end: new Date(),
  };

  const reports = store?.data.reports.filter((r) =>
    fromSearchParam !== null ? isWithinInterval(r.datetime, pastYear) : true
  );

  const data1: Serie[] = currencies.map((currency) => {
    const allReportsInCurrency = _.compact(
      reports?.map((report) => {
        const d = new Date(report.datetime);
        const x =
          d.getUTCFullYear() +
          '-' +
          _.padStart(String(d.getUTCMonth() + 1), 2, '0') +
          '-' +
          d.getUTCDate();
        console.log(
          '=====d.getUTCMonth() === 1',
          d.getUTCMonth() === 1,
          getTotalForReport(store, report, currency),
          x
        );

        // if (d.getUTCMonth() !== 1 && d.getUTCMonth() !== 7) {
        //   return;
        // }
        return {
          // x: report.datetime,
          x,
          // x: '2018-01',
          y: (
            getTotalForReport(
              store,
              report,
              currency,
              (a) => a.name === currency
            ) || 0
          ).toFixed(0),
        };
      })
    );
    console.log('====allReportsInCurrency', allReportsInCurrency);
    return {
      id: currency,
      data: allReportsInCurrency || [],
    };
  });

  const data3: Serie[] = currencies.map((currency) => {
    const allReportsInCurrency = _.compact(
      reports?.map((report) => {
        const d = new Date(report.datetime);
        const x =
          d.getUTCFullYear() +
          '-' +
          _.padStart(String(d.getUTCMonth() + 1), 2, '0') +
          '-' +
          d.getUTCDate();
        console.log(
          '=====d.getUTCMonth() === 1',
          d.getUTCMonth() === 1,
          getTotalForReport(store, report, currency),
          x
        );

        // if (d.getUTCMonth() !== 1 && d.getUTCMonth() !== 7) {
        //   return;
        // }
        return {
          // x: report.datetime,
          x,
          // x: '2018-01',
          y: (
            (getTotalForReport(
              store,
              report,
              currency,
              (a) => a.name === currency
            )! / getTotalForReport(store, report, currency)! || 0) * 100
          ).toFixed(0),
        };
      })
    );
    console.log('====allReportsInCurrency', allReportsInCurrency);
    return {
      id: currency,
      data: allReportsInCurrency || [],
    };
  });

  console.log({ data1 });

  const totalForCurrency = (currencyIdx: any) => {
    return (
      <ResponsiveLine
        animate={false}
        data={[data1[currencyIdx]]}
        margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
        xScale={{
          type: 'time',
          format: '%Y-%m-%d',
          // precision: 'month',
          // useUTC: true,
        }}
        xFormat="time:%Y-%m-%d"
        yScale={{
          // type: 'log',
          // base: 8,
          type: 'linear',
          min: 'auto',
          max: 'auto',
          stacked: true,
          // reverse: false,
        }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
          format: '%Y %m',
          tickValues: 'every 4 months',
          legend: '',
          legendOffset: -12,
        }}
        axisLeft={{
          orient: 'left',
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
        }}
        // colors={{ scheme: 'nivo' }}
        pointSize={0}
        pointColor={{ theme: 'background' }}
        pointBorderWidth={2}
        pointBorderColor={{ from: 'serieColor' }}
        pointLabel="y"
        pointLabelYOffset={-12}
        enableSlices="x"
        useMesh={true}
        legends={[
          {
            anchor: 'bottom-right',
            direction: 'column',
            justify: false,
            translateX: 100,
            translateY: 0,
            itemsSpacing: 0,
            itemDirection: 'left-to-right',
            itemWidth: 80,
            itemHeight: 20,
            itemOpacity: 0.75,
            symbolSize: 12,
            symbolShape: 'circle',
            symbolBorderColor: 'rgba(0, 0, 0, .5)',
            effects: [
              {
                on: 'hover',
                style: {
                  itemBackground: 'rgba(0, 0, 0, .03)',
                  itemOpacity: 1,
                },
              },
            ],
          },
        ]}
        sliceTooltip={({ slice }) => {
          console.log('------slice', slice);
          return (
            <div
              style={{
                background: 'white',
                padding: '9px 12px',
                border: '1px solid #ccc',
              }}
            >
              <div>{slice.points[0].data.xFormatted}</div>
              {slice.points.map((point) => (
                <div
                  key={point.id}
                  style={{
                    color: point.serieColor,
                    padding: '3px 0',
                  }}
                >
                  <strong>{point.serieId}</strong> {point.data.yFormatted}
                </div>
              ))}
            </div>
          );
        }}
      />
    );
  };

  return (
    <div style={{ height: 300 }}>
      <h1>Charts</h1>
      <h6>
        <Link to="?yearsBack=1">1 year</Link> |{' '}
        <Link to="?yearsBack=2">2 year</Link> | <Link to=".">all</Link>
      </h6>
      <div style={{ width: '100%', height: 300, display: 'flex' }}>
        <div style={{ width: '33%', height: 300 }}>{totalForCurrency(0)}</div>
        <div style={{ width: '33%', height: 300 }}>{totalForCurrency(1)}</div>
        <div style={{ width: '33%', height: 300 }}>{totalForCurrency(2)}</div>
      </div>

      <ResponsiveLine
        animate={false}
        data={data3}
        margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
        xScale={{
          type: 'time',
          format: '%Y-%m-%d',
          // precision: 'month',
          // useUTC: true,
        }}
        xFormat="time:%Y-%m-%d"
        yScale={{
          // type: 'log',
          // base: 8,
          type: 'linear',
          min: 'auto',
          max: 'auto',
          // stacked: true,
          // reverse: false,
        }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
          format: '%Y %m',
          tickValues: 'every 4 months',
          legend: '',
          legendOffset: -12,
        }}
        // axisBottom={{
        //   orient: 'bottom',
        //   tickSize: 5,
        //   tickPadding: 5,
        //   tickRotation: 0,
        //   legend: 'date',
        //   legendOffset: 36,
        //   legendPosition: 'middle',
        // }}
        axisLeft={{
          orient: 'left',
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: '%',
          legendOffset: -50,
          legendPosition: 'middle',
        }}
        // colors={{ scheme: 'nivo' }}
        pointSize={4}
        pointColor={{ theme: 'background' }}
        pointBorderWidth={2}
        pointBorderColor={{ from: 'serieColor' }}
        pointLabel="y"
        pointLabelYOffset={-12}
        enableSlices="x"
        useMesh={true}
        legends={[
          {
            anchor: 'bottom-right',
            direction: 'column',
            justify: false,
            translateX: 100,
            translateY: 0,
            itemsSpacing: 0,
            itemDirection: 'left-to-right',
            itemWidth: 80,
            itemHeight: 20,
            itemOpacity: 0.75,
            symbolSize: 12,
            symbolShape: 'circle',
            symbolBorderColor: 'rgba(0, 0, 0, .5)',
            effects: [
              {
                on: 'hover',
                style: {
                  itemBackground: 'rgba(0, 0, 0, .03)',
                  itemOpacity: 1,
                },
              },
            ],
          },
        ]}
        sliceTooltip={({ slice }) => {
          console.log('------slice', slice);
          return (
            <div
              style={{
                background: 'white',
                padding: '9px 12px',
                border: '1px solid #ccc',
              }}
            >
              <div>{slice.points[0].data.xFormatted}</div>
              {slice.points.map((point) => (
                <div
                  key={point.id}
                  style={{
                    color: point.serieColor,
                    padding: '3px 0',
                  }}
                >
                  <strong>{point.serieId}</strong> {point.data.yFormatted}%
                </div>
              ))}
            </div>
          );
        }}
      />
    </div>
  );
};
