import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import MenuIcon from '@material-ui/icons/Menu';
import React, { useEffect } from 'react';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import _ from 'lodash';
import { AppBar } from '@material-ui/core';
import { Link, useLocation } from 'react-router-dom';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    text: {
      padding: theme.spacing(2, 2, 0),
    },
    paper: {
      paddingBottom: 50,
    },
    list: {
      marginBottom: theme.spacing(2),
    },
    subheader: {
      backgroundColor: theme.palette.background.paper,
    },
    appBar: {
      top: 'auto',
      bottom: 0,
    },
    grow: {
      flexGrow: 1,
    },
    fabButton: {
      position: 'absolute',
      zIndex: 1,
      top: -30,
      left: 0,
      right: 0,
      margin: '0 auto',
    },
    // list: {
    //   width: 250,
    // },
    fullList: {
      width: 'auto',
    },
  })
);

const locations = {
  0: 'assets',
  1: 'asset-accounts',
  2: 'charts',
  3: 'institutions',
} as const;

type Locations = keyof typeof locations;
const locs = _.values(locations);

export const BottomAppBar: React.FC = () => {
  const classes = useStyles();
  const location = useLocation();

  const [value, setValue] = React.useState<Locations | undefined>(undefined);

  useEffect(() => {
    if (!locs.some((l, idx) => location.pathname.startsWith('/' + l))) {
      setValue(undefined);
    }
    const idx = locs.findIndex(l => location.pathname.startsWith('/' + l));
    if (idx !== -1) {
      setValue(idx as Locations);
    }

  }, [location.pathname, value]);

  return (
    <AppBar position="fixed" color="primary" className={classes.appBar}>
      <BottomNavigation
        value={value}
        onChange={(event, newValue) => {
          console.log({ event, newValue });
          setValue(newValue);
        }}
        showLabels
      >
        <BottomNavigationAction
          component={Link}
          to="assets"
          label="Assets"
          icon={<MenuIcon />}
        />
        <BottomNavigationAction
          component={Link}
          to="asset-accounts"
          label="Asset accounts"
          icon={<RestoreIcon />}
        />
        <BottomNavigationAction
          component={Link}
          to="charts"
          label="Charts"
          icon={<FavoriteIcon />}
        />
        <BottomNavigationAction
          component={Link}
          to="institutions"
          label="Institutions"
          icon={<LocationOnIcon />}
        />
      </BottomNavigation>
    </AppBar>
  );
};
