import React, { useContext } from 'react';
import { Interpreter } from 'xstate';
import _ from 'lodash';

import { Table, Tr, Th, Td } from './components/Table';
import { StoreContext } from './App';
import { fintrack } from '../typings/fintrack';
import { StoreEvent } from './machines/store';
import { Link } from 'react-router-dom';

type OwnProps = {
  storeService: Interpreter<any, any, StoreEvent, any>;
};

function getExchangeRate(
  store: fintrack.JSONShape | undefined,
  erData: fintrack.ExchangeRateData | undefined,
  // report: fintrack.Report,
  asset: fintrack.Asset,
  targetCurrency: fintrack.Currency
): number {
  if (erData === undefined) {
    return 0;
  }
  const isBaseAssetACurrency = asset.class === 'CURRENCY';
  // if (asset.currency === targetCurrency) {
  //   return 1;
  // }

  // then find such asset where class is currency, name is asset.currency
  // and check whether currency of such asset is `fintrack.BaseCurrency` (USD)
  const currencyAsset = isBaseAssetACurrency
    ? asset
    : store?.data.assets.find(
        (a) => a.class === 'CURRENCY' && a.name === asset.currency
      )!;
  let erToBaseCurrency = 1;

  if (currencyAsset.currency !== targetCurrency) {
    const currencyAssetToBase = store?.data.assets.find(
      (a) => a.class === 'CURRENCY' && a.name === targetCurrency
    );
    if (currencyAssetToBase !== undefined) {
      const er = erData[currencyAssetToBase.id];
      if (er !== null) {
        // console.log({ er })
        erToBaseCurrency = parseFloat(er!);
      } else {
        erToBaseCurrency = 0;
      }
    }
  }
  if (erToBaseCurrency === 0) {
    return 0;
  }
  // console.log({ targetCurrency, asset, currencyAsset, erToBaseCurrency });
  return (
    (parseFloat(erData[currencyAsset.id] || '0') / erToBaseCurrency) *
    (isBaseAssetACurrency ? 1 : parseFloat(erData[asset.id] || '0'))
  );
}

export function getTotalForReport(
  store: fintrack.JSONShape | undefined,
  report: fintrack.Report,
  targetCurrency: fintrack.Currency,
  assetFilterFunction?: (asset: fintrack.Asset) => boolean
): number | undefined {
  return store?.data.assetAccountStates
    .filter((aas) => aas.reportId === report.id)
    .reduce((acc, currVal) => {
      const assetAccount = store.data.assetAccounts.find(
        (aa) => aa.id === currVal.assetAccountId
      )!;
      const asset = store.data.assets.find(
        (a) => a.id === assetAccount.assetId
      )!;
      if (assetFilterFunction !== undefined && !assetFilterFunction(asset)) {
        return Math.round(acc);
      }
      // asset.currency
      const erData = store.data.exchangeRates.find(
        (er) => er.reportId === report.id
      )?.data;
      // const erToAssetCurrency = erData[asset.id] ?? '0';
      let erFromAssetToTarget = getExchangeRate(
        store,
        erData,
        asset,
        targetCurrency
      );
      const assetPriceInBaseCurrency =
        // parseFloat(erToAssetCurrency) *
        erFromAssetToTarget * parseFloat(currVal.balance);
      // if (asset.currency !== baseCurrency) {
      // }
      // if (report.id === 'UJ1XJQ') {
      //   console.log(
      //     'targetCurrency',
      //     targetCurrency,
      //     'asset',
      //     asset,
      //     'erFromAssetToBase',
      //     erFromAssetToBase,
      //     't',
      //     assetPriceInBaseCurrency
      //   );
      // }
      return Math.round(assetPriceInBaseCurrency + acc);
    }, 0);
}

export function getInstitutions(
  store: fintrack.JSONShape | undefined
): fintrack.Institution[] | undefined {
  return _.sortBy(store?.data.institutions, (i) => i.sortIndex || i.name);
}

export function getAssetAccounts(
  store: fintrack.JSONShape | undefined
): fintrack.AssetAccount[] | undefined {
  return getInstitutions(store)?.flatMap((inst) => {
    return _.sortBy(
      store?.data.assetAccounts.filter((aa) => aa.institutionId === inst.id),
      // TODO replace name with sort order
      (aa) => aa.name
    );
  });
}

export const Home: React.FC<OwnProps> = (props) => {
  const store = useContext(StoreContext);

  const assetAccounts = getAssetAccounts(store);
  if (assetAccounts === undefined) {
    return null;
  }
  const sortedReports = _.sortBy(store?.data.reports, (r) => -1 * r.datetime);
  const assets = store?.data.assets;
  const lastReport = sortedReports[0]!;

  const deleteReport = (reportId: string) => {
    props.storeService.send('UPDATE_DATA', {
      actionType: 'delete',
      entity: 'reports',
      data: {
        id: reportId,
      },
    });
  };

  const updateExchangeRate = (
    value: string,
    asset: fintrack.Asset,
    report: fintrack.Report
  ) => {
    console.log('_________value', value, asset, report);

    props.storeService.send({
      type: 'UPDATE_DATA',
      actionType: 'update',
      entity: 'exchangeRates',
      data: {
        value,
        asset,
        report,
      },
    });
  };

  return (
    <div>
      <h1>Your net worth is...</h1>
      <Table>
        <thead>
          <Tr>
            <Th></Th>
            {getInstitutions(store)?.map((inst) => {
              return (
                <Th
                  colspan={
                    assetAccounts.filter((aa) => aa.institutionId === inst.id)
                      .length
                  }
                >
                  {inst.name}
                </Th>
              );
            })}
          </Tr>
          <Tr>
            <Th>date</Th>
            {assetAccounts.map((aa) => (
              <Th>{aa.name}</Th>
            ))}
            <Th>Total in RUB</Th>
            <Th>Total in USD</Th>
            <Th>Total in EUR</Th>
          </Tr>
        </thead>
        <tbody>
          {sortedReports?.map((report) => {
            const assetAccountStates = store?.data.assetAccountStates.filter(
              (aas) => aas.reportId === report.id
            );
            return (
              <Tr key={report.id}>
                <Td>
                  <Link to={`/add-edit-report/${report.id}`}>
                    {new Date(report.datetime).toISOString().substr(0, 10)}
                  </Link>
                </Td>
                {assetAccounts.map((aa) => {
                  const aas = assetAccountStates?.find(
                    (aas) => aas.assetAccountId === aa.id
                  );
                  return <Td>{aas?.balance}</Td>;
                })}
                <Td>{getTotalForReport(store, report, 'RUB')}</Td>
                <Td>{getTotalForReport(store, report, 'USD')}</Td>
                <Td>{getTotalForReport(store, report, 'EUR')}</Td>
                <Td>
                  <button
                    onClick={() => {
                      deleteReport(report.id);
                    }}
                  >
                    delete
                  </button>
                </Td>
              </Tr>
            );
          })}
        </tbody>
      </Table>
      <h2>Exchange rates</h2>
      <Table>
        <thead>
          <Tr>
            <Th>date</Th>
            {assets?.map((a) => (
              <Th>
                {a.class} {a.subclass} {a.name} in {a.currency}
              </Th>
            ))}
          </Tr>
        </thead>
        <tbody>
          {sortedReports?.map((report) => {
            const exchangeRate = store?.data.exchangeRates.find(
              (er) => er.reportId === report.id
            );
            return (
              <Tr key={report.id}>
                <Td>{new Date(report.datetime).toISOString().substr(0, 10)}</Td>
                {assets?.map((a) => {
                  const rate = exchangeRate?.data?.[a.id];
                  // const aas = exchangeRate.find(
                  //   (aas) => aas.assetAccountId === aa.id
                  // );
                  return (
                    <Td>
                      <input
                        style={{ width: 50 }}
                        defaultValue={rate}
                        onChange={(v) =>
                          updateExchangeRate(v.target.value, a, report)
                        }
                      />
                    </Td>
                  );
                })}
              </Tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
};
